

import Vue from 'vue'
import App from './App'
import router from './router'
import VueScroller from 'vue-scroller'
import store from './store'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

Vue.use(ToastPlugin);

Vue.use(VueScroller);

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
template: '<App/>',
components: { App }
})



