
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Sc from 'vux/src/components/scroller/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'


const refreshKey = ['A', 'B01', 'B02', 'B03', 'B04', 'B05', 'B06', 'B07', 'B08', 'B09', 'B010'];
let refreshKeyIndex = 0;
let page =0;
let prepage = 10;
let firstLoaded = false;
let moreLoaded = true;
function getRefreshKey() {
refreshKeyIndex++;
if (refreshKeyIndex >= refreshKey.length) {
refreshKeyIndex = 0;
}
return refreshKey[refreshKeyIndex];
}


export default {
name: 'app',
components: {
ViewBox,
XHeader,
Tab,
TabItem,
Sc,
Tabbar,
TabbarItem
},
mounted() {
this.$store.dispatch('getTopList').then(() => {
setTimeout(() => {
firstLoaded = true;
}, 300);
});
},
computed: {
categoryKey() {
return this.$store.getters.categoryKey;
},
categories() {
return this.$store.getters.categories;
}
},
methods: {
refresh() {
setTimeout(() => {
this.$store.dispatch('refreshTopList', {
refreshKey: getRefreshKey()
})
.then(n => {
this.$refs.myScroller.finishPullToRefresh();



this.$vux.toast.text(`成功为您推荐${n}条新内容`, 'top');
})
}, 1000);
},
infinite() {
if (!firstLoaded) {
this.$refs.myScroller.finishInfinite();
return;
}
if (!moreLoaded) {
return;
}


moreLoaded = false;
page++;
setTimeout(() => {
this.$store.dispatch('loadMoreList', {
refreshKey: refreshKey[refreshKeyIndex],
page,
prepage
})
.then(() => {
this.$refs.myScroller.finishInfinite();

moreLoaded = true;
})
}, 1000);
}
}
}



